.btnFound, .btnLost {
    position: absolute;
    width: 600px;
    font-family: 'Montserrat', Helvetica, sans-serif;
    overflow: hidden;
    border-radius: 0 80px 80px 0;
    border: 0;
    display: flex;
    justify-content: space-between;
    left: -200px;
    z-index: 1;
}

.btnLost {
    position: relative;
    background-color: #fa5456;
    margin-bottom: 10px;
    font-size: 32px;
    color: white;
    text-align: center;
}

.btnLost h2, .btnFound h2 {
    font-weight: 500;
    font-size: 32px;
    margin: 30px 0 0 200px;
}
.btnLostNav{
    width: 120px;
    height: 30px;
    background-color: #fa5456;
    color: white;
    border: 0;
    border-radius: 15px;
    font-size: 14px;
    margin-top: 20px;
    margin-right: 10px;
    font-family: 'Montserrat', Helvetica, sans-serif;
}
.btnLostNav:hover{
    background-color: #ffd219;
    color: black;
    cursor: pointer;
    transition: 0.2s ease;
}

.btnLost img {
    margin: 20px 0 0 0;
}
.btnLost:hover{
    background-color: #ffd219;
    transition: 0.2s ease;
    cursor: pointer;
}
.btnLost::before{
    content: 'I lost my pet!';
    color: black;
    padding: 30px 0 0 250px;
}
.btnLost:hover::before{
    content: 'Click to find';
}

.btnFound {
    position: relative;
    background-color: #06B2BB;
    height: 98px;
    width: 90%;
    color: white;
    font-size: 30px;
    text-align: center;
    padding: 30px 0 0 250px;
}
.btnFound:hover{
    background-color: rgba(6, 178, 187, 0.87);
    cursor: pointer;
}
.btnFound::before{
    content: 'I found a pet!';
}
.btnFound:hover::before{
